/*
 * Стили компонента Searchbar
 */
.searchbar {
  top: 0;
  left: 0;
  position: sticky;
  // z-index: 1100;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 64px;
  padding-right: 24px;
  padding-left: 24px;
  padding-top: 12px;
  padding-bottom: 12px;
  color: #fff;
  background-color: #3f51b5;
  box-shadow: 0px 2px 4px -1px rgba(0, 0, 0, 0.2),
    0px 4px 5px 0px rgba(0, 0, 0, 0.14), 0px 1px 10px 0px rgba(0, 0, 0, 0.12);
}

/*
 * Стили компонента SearchForm
 */
.form {
  display: flex;
  align-items: center;
  width: 100%;
  max-width: 600px;
  background-color: #fff;
  border-radius: 3px;
  overflow: hidden;
}

.button {
  display: inline-block;
  // width: 48px;
  // height: 48px;
  // border: 0;
  // background-image: url('https://image.flaticon.com/icons/svg/149/149852.svg');
  // background-size: 40%;
  // background-repeat: no-repeat;
  // background-position: center;
  // background-color: rgb(135, 92, 92);
  // opacity: 0.6;
  // transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1);
  cursor: pointer;
  outline: none;
  // проверить свг ссылку
  background-color: rgb(98, 109, 232);
  color: #eee;
  padding: 8px 10px;
  margin: 3px;
}

.button:hover {
  opacity: 1;
}

.button__label {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  clip-path: inset(50%);
  border: 0;
  // color: black;
}

.input {
  display: inline-block;
  width: 100%;
  font: inherit;
  color: black;
  font-size: 20px;
  border: none;
  outline: none;
  padding-left: 4px;
  padding-right: 4px;
}

.input::placeholder {
  font: inherit;
  font-size: 18px;
  color: rgb(134, 134, 134);
}
